p {
  margin-top: 0;
  margin-bottom: 1rem; }

* {
  box-sizing: border-box; }

.btn {
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  border-radius: 0.25rem;
  position: relative;
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  white-space: nowrap;
  text-align: center;
  vertical-align: middle;
  background-image: none;
  border: 1px solid transparent;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.15), 0 1px 1px rgba(0, 0, 0, 0.075);
  cursor: pointer;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  outline: none;
  text-decoration: none; }
  .btn.disabled, .btn[disabled] {
    cursor: not-allowed;
    opacity: 0.65;
    box-shadow: none; }
    .btn.disabled:focus, .btn[disabled]:focus {
      pointer-events: none; }

.btn-sm {
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  border-radius: 0.3rem; }

.btn-lg {
  padding: 0.5rem 1rem;
  font-size: 1.25rem;
  border-radius: 0.2rem; }

.btn-primary {
  color: #fff;
  background-color: #0d6efd;
  border-color: #0d6efd; }
  .btn-primary:hover {
    color: #fff;
    background-color: #3385fd;
    border-color: #408cfd; }
  .btn-primary:focus {
    color: #fff;
    background-color: #3385fd;
    border-color: #408cfd; }
  .btn-primary.disabled, .btn-primary[disabled] {
    color: #fff;
    background-color: #0d6efd;
    border-color: #0d6efd; }

.btn-danger {
  color: #fff;
  background-color: #dc3545;
  border-color: #dc3545; }
  .btn-danger:hover {
    color: #fff;
    background-color: #e25663;
    border-color: #e4606d; }
  .btn-danger:focus {
    color: #fff;
    background-color: #e25663;
    border-color: #e4606d; }
  .btn-danger.disabled, .btn-danger[disabled] {
    color: #fff;
    background-color: #dc3545;
    border-color: #dc3545; }

.btn-default {
  color: #212529;
  background-color: #fff;
  border-color: #adb5bd; }
  .btn-default:hover {
    color: #0d6efd;
    background-color: #fff;
    border-color: #0d6efd; }
  .btn-default:focus {
    color: #0d6efd;
    background-color: #fff;
    border-color: #0d6efd; }
  .btn-default.disabled, .btn-default[disabled] {
    color: #212529;
    background-color: #fff;
    border-color: #adb5bd; }

.btn-link {
  font-weight: 400;
  color: #0d6efd;
  text-decoration: none;
  box-shadow: none; }
  .btn-link:hover, .btn-link:focus {
    color: #024dbc;
    text-decoration: none; }
  .btn-link.disabled:hover, .btn-link.disabled:focus {
    color: #0d6efd;
    text-decoration: none; }
