p {
  margin-top: 0;
  margin-bottom: 1rem; }

* {
  box-sizing: border-box; }

.menu {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 30px;
  list-style: none;
  border-bottom: 1px solid #dee2e6;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.15), 0 1px 1px rgba(0, 0, 0, 0.075); }
  .menu .menu-item {
    padding: 0.5rem 1rem;
    transition: color 0.15s ease-in-out, border-color 0.15s ease-in-out;
    cursor: pointer;
    border-bottom: 2px solid transparent; }
    .menu .menu-item:hover, .menu .menu-item.active {
      text-decoration: none; }
    .menu .menu-item.is-disabled {
      color: #6c757d;
      cursor: not-allowed; }
    .menu .menu-item:not(.is-disabled):hover, .menu .menu-item:not(.is-disabled).is-active {
      color: #0d6efd;
      border-bottom-color: #0d6efd; }
    .menu .menu-item.is-open .arrow-icon {
      transform: rotate(-180deg); }
  .menu .menu-submenu-item .menu-submenu-title {
    display: flex;
    align-items: center; }
    .menu .menu-submenu-item .menu-submenu-title .arrow-icon {
      font-size: 1rem;
      margin-left: 5px;
      transition: transform 0.2s ease-in-out; }
  .menu .menu-submenu-item .menu-submenu {
    padding-left: 0;
    list-style: none;
    white-space: nowrap; }
    .menu .menu-submenu-item .menu-submenu .menu-submenu-content {
      border: 1px solid #dee2e6;
      background-color: #fff;
      box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04); }
      .menu .menu-submenu-item .menu-submenu .menu-submenu-content .menu-item {
        color: #212529; }
        .menu .menu-submenu-item .menu-submenu .menu-submenu-content .menu-item:not(.is-disabled):hover, .menu .menu-submenu-item .menu-submenu .menu-submenu-content .menu-item:not(.is-disabled).is-active {
          color: #0d6efd;
          border-bottom-color: transparent;
          border-left-color: transparent; }

.menu-horizontal .menu-submenu-item {
  position: relative; }
  .menu-horizontal .menu-submenu-item .menu-submenu {
    z-index: 100;
    position: absolute;
    top: calc(100%);
    left: 0;
    padding-top: 6px; }

.menu-vertical {
  flex-direction: column; }
  .menu-vertical .menu-item {
    border-bottom-width: 0;
    border-left: 2px solid transparent; }
    .menu-vertical .menu-item:not(.is-disabled):hover, .menu-vertical .menu-item:not(.is-disabled).is-active {
      border-bottom-color: transparent;
      border-left-color: #0d6efd; }
  .menu-vertical .menu-submenu {
    padding-top: 0.5rem; }
